body{
    margin: 0;
    overflow-x: hidden;
    background-color: #222831;
    font-family:sans-serif;
}

.Main{
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100vw;
    text-align: center;
}

#logo{
    font-size: 64px;
    color: white;
    padding: 1rem 5rem;
    margin-top: -10rem;
    box-shadow: 0 17px 19px 0 rgba(0, 0, 0, 0.2), 0 16px 120px 0 rgba(0, 0, 0, 0.19);
}

#logo span{
    color: #3F72AF;
}

.mainContainer{
    height: 50vh;
    width: 60%;
    border-radius: 2px;
    box-shadow: 0 17px 19px 0 rgba(0, 0, 0, 0.2), 0 16px 120px 0 rgba(0, 0, 0, 0.19);
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #393E46;
    color: white;
    margin-bottom: -3rem;
}

.formContainer{
    display: flex;
    width: 40%;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 1rem;
}

.formContainer label{
    font-size: 28px;
}

.inputTask{
    width: 10vw;
    padding: 1rem 1.5rem;
    border: 1px solid black;
    outline: none;
    border-radius: 10px;
    font-size: 18px;
}

.btn{
    width: 10vw;
    padding: 1rem 1.5rem;
    border: 1px solid black;
    outline: none;
    border-radius: 10px;
    cursor: pointer;
    background-color: #3F72AF;
}

.taskContainer{
    box-sizing: border-box;
    height: 50vh;
    overflow-y: scroll;
    overflow-x: hidden;
    border-radius: 5px;
    box-shadow: 0 17px 19px 0 rgba(0, 0, 0, 0.2), 0 16px 120px 0 rgba(0, 0, 0, 0.19);
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 60%;
}

.taskContainer h1{
    background-color: #3F72AF;
    color: white;
    border: 2px solid white;
    text-align: center;
    padding: .8rem 0rem;
    width: 15vw;
}

img{
    position: absolute;
    width: 35vw;
    height: 35vh;
    opacity: 0.9;
}

#img1{
    top: 2rem;
    left: -4rem;
    z-index: -1;
}


#img3{
    bottom: 1rem;
    right: -3rem;
}

.theTask{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    width: 20vw;
}

.theTask img{
    position: relative;
    cursor: pointer;
    opacity: 1;
    width: 2rem;
    height: 2rem;
}
.theTask img:hover{
    box-shadow: 0 17px 19px 0 rgba(0, 0, 0, 0.2), 0 16px 120px 0 rgba(0, 0, 0, 0.19);
}

.hidden{
    display: none;
}

.formContainer label{
    font-size: 48px;
}

@media screen and (max-width:400px) {

    .Main{
        height:max-content;
    }

    #logo{
        margin-top: 0rem;
    }

    .mainContainer{
        width: 80%;
        height: 100vh;
        flex-direction: column;
        padding: 1rem;
        gap: 1rem;
    }

    .formContainer{
        width: 100%;
    }

    .inputTask{
        width: 70%;
    }

    .btn{
        width: 70%;
    }

    .taskContainer{
        width: 100%;
        height: 70vh;
        border-left: none;
    }

    img{
        width: 52vw;
    }

    #img3{
        bottom: -27rem;
        right: 1rem;
    }

    .theTask {
        width: 100% !important;
    }
    
    .theTask h1{
        width: 100% !important;
    }

    
}